.custom-sidebar {
  background: #fff;
  box-shadow: 2px 0px 5px rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  height: 80%;
  width: 100%;
  z-index: 300;
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
  transition: -webkit-transform 0.3s ease-out;
  transition: transform 0.3s ease-out;
  transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
}
.custom-sidebar * {
  padding: 0;
  margin: 0;
}
.custom-sidebar.open {
  -webkit-transform: translateY(0);
          transform: translateY(0);
}
.custom-sidebar ul {
  list-style: none;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  padding: 20px;
  height: 100%;
  overflow-y: auto;
}
.custom-sidebar li {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  height: 100%;
}
.custom-sidebar a {
  text-decoration: none;
  color: black;
  font-size: 15px;
  text-transform: uppercase;
  display: inline-block;
  margin: 0.9rem 0;
}
.custom-sidebar .center-items-v{
    -webkit-flex-direction: column;
            flex-direction: column;
}
.custom-sidebar a svg {
  margin-right: 5px;
}
.custom-toggle-button svg {
  color: #fff;
  cursor: pointer;
}
.close-icon svg {
  color: rgb(221, 0, 0);
  cursor: pointer;
}
.close-icon svg:hover {
  color: rgb(255, 0, 0);
}

/* Responsive */

@media (min-width: 768px) {
    .custom-sidebar {
      display: none;
    }
    .custom-toggle-button {
      display: none;
    }
  }

