.auth-container {
  min-height: 90vh;
  width: 70vw;
  margin: auto;
  padding: 9rem 0;
}
.auth-container h2 {
  margin-bottom: 1.5rem;
}
form {
  width: 60%;
}
input[type="text"],
input[type="email"],
input[type="password"] {
  font-family: "Inter";
  font-weight: 400;
  font-size: 14px;
  background: #fff;
  border: 1px solid rgba(0, 0, 0, 0.11);
  line-height: 28px !important;
  height: auto;
  color: #000;
  padding: 7px 15px;
  transition: all 200ms ease;
  border-radius: 0;
  resize: none;
  position: relative;
  width: 100%;
  box-sizing: border-box;
}
button[type="submit"] {
  font-family: "Inter";
  font-weight: 500;
  font-size: 13px;
  line-height: 16px;
  text-align: center;
  display: inline-block;
  padding: 12px 30px;
  margin-top: 20px;
  max-width: 100%;
  cursor: pointer;
  text-align: center;
  margin-bottom: 5px;
  transition: all 300ms ease;
  color: #fff;
  background: #000;
  border: 1px solid #000;
  position: relative;
  border-radius: 0;
  -moz-border-radius: 0;
  -webkit-border-radius: 0;
}
button[type="submit"]:hover {
  background: #fff;
  color: #000;
}
form .error {
  font-size: 12px;
  color: red;
}
@media (max-width: 768px) {
  form {
    width: 80%;
  }
  .auth-container h2 {
    font-size: 20px;
  }
}
@media (max-width: 480px) {
  form {
    width: 100%;
  }
}

